.menu-container {
  --menu-padding: 69px;

  position: absolute;
  top: 2em;
  left: 0;
  display: flex;
  justify-content: space-between;
  font-family: var(--sans-serif);
  color: var(--deep);
  width: calc(100vw - 2 * var(--menu-padding));
  padding: 0 var(--menu-padding);
}

.menu-right .menu-link + .menu-link {
  margin-left: 18px;
}

.menu-link {
  color: var(--deep);
  text-decoration: none;
}

.menu-container .menu-link:hover {
  color: var(--black);
}

@media only screen and (max-width: 760px) {
  .menu-container {
    --menu-padding: 42px;
  }
}
