.super-container {
  display: flex;
  justify-content: center;
  width: 100%;
}

.about-container {
  margin: var(--header-marginTop) 2em 4em;
  width: calc(100% - 4em);
  max-width: 900px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.about-container img {
  max-width: 100%;
}

.about-me {
  margin-top: 16vh;
}

.about-blurb {
  max-width: 400px;
}

.about-rightNow {
  margin-top: 2em;
}

.about-rightNowTitle {
  color: var(--deep);
  font-family: var(--sans-serif);
  font-weight: 600;
  font-size: 1.2em;
}

.about-rightNowList {
  margin-top: 0.5em;
}

.about-rightNowList span + span {
  margin-left: 0.6em;
}

.about-container a {
  position: relative;
  color: var(--black);
  text-decoration: none;
}

.about-container a::before {
  content: "";
  position: absolute;
  background-color: var(--yella);
  width: 100%;
  height: 0.4em;
  left: 0;
  bottom: 0;
  z-index: -1;
  transition: height 0.1s;
}

.about-container a:hover::before {
  height: 0.7em;
}

svg {
  margin-left: -10px;
  max-width: 240px;
}

svg .cls-1 {
  fill: var(--grey);
}
svg .cls-1,
svg .cls-2 {
  font-size: 120px;
  font-weight: 600;
  font-family: var(--sans-serif);
}
svg .cls-3 {
  fill: var(--blue);
  width: 36px;
  height: 154px;
  rx: 18px;
  ry: 18px;
  animation: bob-mid 1s ease-in-out infinite;
}

svg .cls-3:nth-child(3n + 0) {
  animation-name: bob-low;
}

svg .cls-3:nth-child(4n + 2) {
  animation-name: bob-low;
}

svg .cls-3:nth-child(4n + 2) {
  animation-delay: -0.4s;
}

svg .cls-3:nth-child(3n - 1) {
  animation-delay: -0.3s;
}

svg .cls-3:nth-child(6n - 1) {
  animation-delay: -0.6s;
}

svg .cls-3:nth-child(5n + 1) {
  animation-name: bob-high;
}

svg .cls-3:nth-child(3n + 4) {
  animation-duration: 0.8s;
}

@keyframes bob-mid {
  from {
    transform: translateY(0);
  }
  50% {
    transform: translateY(70px);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes bob-high {
  from {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(50px);
  }
  to {
    transform: translateY(-10px);
  }
}

@keyframes bob-low {
  from {
    transform: translateY(0);
  }
  50% {
    transform: translateY(50px);
  }
  to {
    transform: translateY(0);
  }
}

@media only screen and (max-width: 760px) {
  .about-container {
    grid-template-columns: 1fr;
  }
}
