.project-container {
  max-width: 700px;
}

.image-container img {
  width: 100%;
}

.image-container {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.image-set .image-container {
  display: inline-block;
}

.image-set--three .image-container {
  width: calc(33% - 3.5px);
}

.project-container .quote {
  padding-left: 1.2em;
  border-left: 4px var(--grey) solid;
  color: var(--deep);
  transition: color 0.2s;
}

.project-container a {
  position: relative;
  color: var(--black);
  text-decoration: none;
}

.project-container a::before {
  content: "";
  position: absolute;
  background-color: var(--yella);
  width: 100%;
  height: 0.4em;
  left: 0;
  bottom: 0;
  z-index: -1;
  transition: height 0.1s;
}

.project-container a:hover::before {
  height: 0.7em;
}

.project-container .sans {
  font-family: var(--sans-serif);
}
