:root {
  --red: #e16163;
  --orange: #f8b026;
  --yella: #ffe450;
  --blue: #76cebf;
  --white: #fff;
  --grey: #cad6dc;
  --deep: #708a9d;
  --black: #000;

  --serif: "IBM Plex Serif", serif;
  --sans-serif: "Century Gothic", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  --code: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html {
  overflow-x: hidden;
}

.u-pointer {
  cursor: pointer;
}

.super-container {
  display: flex;
  justify-content: center;
  width: 100%;
}

.child-container {
  margin: var(--header-marginTop) 2em 4em;
  width: calc(100% - 4em);
  max-width: 900px;
}

h2 {
  color: var(--deep);
  font-family: var(--sans-serif);
  font-weight: 600;
  font-size: 1.2em;
}
