.SongsOfSilence-buttonContainer {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
}

.SongsOfSilence-button {
  border: none;
  background: none;
  color: var(--deep);
  font-size: 0.85em;
  font-family: var(--code);
}

.SongsOfSilence-button:hover {
  color: var(--black);
}

.SongsOfSilence-button--hide {
  visibility: hidden;
}

.SongsOfSilence-passwordInput {
  border: none;
  border-bottom: 1px solid var(--deep);
  font-size: 1em;
  font-family: var(--serif);
}

.SongsOfSilence-reddit p {
  font-family: var(--code);
  margin: 0.8em 0;
}
.SongsOfSilence-reddit em {
  color: var(--deep);
}
.SongsOfSilence-reddit blockquote {
  margin: auto 0 0 2em;
}
