@import url(https://fonts.googleapis.com/css?family=Questrial);
:root {
  --red: #e16163;
  --orange: #f8b026;
  --yella: #ffe450;
  --blue: #76cebf;
  --white: #fff;
  --grey: #cad6dc;
  --deep: #708a9d;
  --black: #000;

  --serif: "IBM Plex Serif", serif;
  --sans-serif: "Century Gothic", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  --code: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html {
  overflow-x: hidden;
}

.u-pointer {
  cursor: pointer;
}

.super-container {
  display: flex;
  justify-content: center;
  width: 100%;
}

.child-container {
  margin: var(--header-marginTop) 2em 4em;
  width: calc(100% - 4em);
  max-width: 900px;
}

h2 {
  color: #708a9d;
  color: var(--deep);
  font-family: "Century Gothic", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  font-family: var(--sans-serif);
  font-weight: 600;
  font-size: 1.2em;
}

::-moz-selection {
  color: var(--black);
  background: var(--yella);
}

::selection {
  color: var(--black);
  background: var(--yella);
}

body {
  margin: 0;
  font-family: var(--serif);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--black);
}

code {
  font-family: var(--code);
}

h1,
h2 {
  font-family: var(--sans-serif);
}

.menu-container {
  --menu-padding: 69px;

  position: absolute;
  top: 2em;
  left: 0;
  display: flex;
  justify-content: space-between;
  font-family: var(--sans-serif);
  color: var(--deep);
  width: calc(100vw - 2 * var(--menu-padding));
  padding: 0 var(--menu-padding);
}

.menu-right .menu-link + .menu-link {
  margin-left: 18px;
}

.menu-link {
  color: var(--deep);
  text-decoration: none;
}

.menu-container .menu-link:hover {
  color: var(--black);
}

@media only screen and (max-width: 760px) {
  .menu-container {
    --menu-padding: 42px;
  }
}

:root {
  --header-marginTop: 6em;
}

.header-container {
  padding: 0 20px;
  margin-top: 6em;
  margin-top: var(--header-marginTop);
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.header-title {
  margin: 0 0 0.4em 0;
  font-size: 3em;
  color: var(--red);
}

.header-social {
  margin: 0 0 1.4em 0;
  display: flex;
  width: 240px;
  justify-content: space-between;
  align-items: center;
}

.header-icon {
  max-height: 20px;
  max-width: 30px;
}

.header-social:hover .header-icon {
  opacity: 0.6;
}

.header-social .header-icon:hover {
  opacity: 1;
}

.header-blurb {
  max-width: 400px;
  font-size: 1em;
}

.header-blurb a {
  position: relative;
  color: var(--black);
  text-decoration: none;
}

.header-blurb a::before {
  content: "";
  position: absolute;
  background-color: var(--yella);
  width: 100%;
  height: 0.4em;
  left: 0;
  bottom: 0;
  z-index: -1;
  -webkit-transition: height 0.1s;
  transition: height 0.1s;
}

.header-blurb a:hover::before {
  height: 0.7em;
}

.footer {
  font-family: var(--code);
  font-size: 0.8em;
  text-align: center;
  margin: 1em 0 3em;
}

:root {
  --thumb-maxWidth: 500px;
}

.thumb-container {
  position: relative;
  max-width: 100%;
  margin-bottom: var(--thumb-gap);
}

.thumb-hoverContainer {
  --topPadding: 20px;
  --leftPadding: 35px;

  position: absolute;
  top: 0;
  left: 0;
  padding: var(--topPadding) var(--leftPadding);
  width: calc(100% - 2 * var(--leftPadding));
  height: calc(100% - 2 * var(--topPadding));
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.7);

  opacity: 0;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.thumb-container:hover .thumb-hoverContainer {
  opacity: 1;
}

.thumb-title {
  font-family: var(--sans-serif);
  font-weight: 600;
  font-size: 2em;
}

.thumb-art {
  max-width: 100%;
}

.thumb-linkContainer {
  margin-top: 1em;
  display: flex;
  justify-content: center;
}

.thumb-link {
  padding: 1px 2px;
  color: var(--black);
  background-color: var(--white);
  font-family: var(--code);
  font-size: 0.8em;
  text-decoration: none;
}

.thumb-link:hover {
  background-color: var(--yella);
}

.thumb-link + .thumb-link {
  margin-left: 8px;
}

.thumb-soon {
  padding: 1px 2px;
  color: var(--black);
  background-color: var(--white);
  font-family: var(--code);
  font-size: 0.8em;
  font-style: italic;
  text-decoration: none;
}

@media only screen and (max-width: 760px) {
  :root {
    --thumb-gap: 10px;
  }

  .thumb-hoverContainer {
    --leftPadding: 10px;
  }
}

:root {
  --thumb-gap: 1em;
}

.projectThumbs-container {
  margin-top: 3em;
}

.projectThumbs-filterContainer {
  display: flex;
  justify-content: center;
  margin-bottom: 2.4em;
  font-family: var(--sans-serif);
  color: var(--deep);
}

.projectThumbs-filterContainer .projectThumbs-filter + .projectThumbs-filter {
  margin-left: 16px;
}

.projectThumbs-filter {
  position: relative;
}

.projectThumbs-filter::after {
  content: "";
  position: absolute;
  width: 0px;
  height: 1px;
  background-color: var(--deep);
  top: 50%;
  left: 0;
  -webkit-transition: width 0.2s;
  transition: width 0.2s;
}

.projectThumbs-filter:hover::after,
.projectThumbs-filter--selected::after {
  width: 100%;
}

.projectThumbs-thumbContainer {
  display: grid;
  margin: auto;
  max-width: 900px;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1em;
  grid-gap: var(--thumb-gap);
}

.projectThumbs-thumbContainer > div:first-child {
  margin-top: 40px;
}

@media only screen and (max-width: 760px) {
  .projectThumbs-thumbContainer {
    grid-template-columns: repeat(1, 1fr);
  }
}

.super-container {
  display: flex;
  justify-content: center;
  width: 100%;
}

.about-container {
  margin: var(--header-marginTop) 2em 4em;
  width: calc(100% - 4em);
  max-width: 900px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.about-container img {
  max-width: 100%;
}

.about-me {
  margin-top: 16vh;
}

.about-blurb {
  max-width: 400px;
}

.about-rightNow {
  margin-top: 2em;
}

.about-rightNowTitle {
  color: var(--deep);
  font-family: var(--sans-serif);
  font-weight: 600;
  font-size: 1.2em;
}

.about-rightNowList {
  margin-top: 0.5em;
}

.about-rightNowList span + span {
  margin-left: 0.6em;
}

.about-container a {
  position: relative;
  color: var(--black);
  text-decoration: none;
}

.about-container a::before {
  content: "";
  position: absolute;
  background-color: var(--yella);
  width: 100%;
  height: 0.4em;
  left: 0;
  bottom: 0;
  z-index: -1;
  -webkit-transition: height 0.1s;
  transition: height 0.1s;
}

.about-container a:hover::before {
  height: 0.7em;
}

svg {
  margin-left: -10px;
  max-width: 240px;
}

svg .cls-1 {
  fill: var(--grey);
}
svg .cls-1,
svg .cls-2 {
  font-size: 120px;
  font-weight: 600;
  font-family: var(--sans-serif);
}
svg .cls-3 {
  fill: var(--blue);
  width: 36px;
  height: 154px;
  rx: 18px;
  ry: 18px;
  -webkit-animation: bob-mid 1s ease-in-out infinite;
          animation: bob-mid 1s ease-in-out infinite;
}

svg .cls-3:nth-child(3n + 0) {
  -webkit-animation-name: bob-low;
          animation-name: bob-low;
}

svg .cls-3:nth-child(4n + 2) {
  -webkit-animation-name: bob-low;
          animation-name: bob-low;
}

svg .cls-3:nth-child(4n + 2) {
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}

svg .cls-3:nth-child(3n - 1) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}

svg .cls-3:nth-child(6n - 1) {
  -webkit-animation-delay: -0.6s;
          animation-delay: -0.6s;
}

svg .cls-3:nth-child(5n + 1) {
  -webkit-animation-name: bob-high;
          animation-name: bob-high;
}

svg .cls-3:nth-child(3n + 4) {
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
}

@-webkit-keyframes bob-mid {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(70px);
            transform: translateY(70px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes bob-mid {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(70px);
            transform: translateY(70px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes bob-high {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  50% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes bob-high {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  50% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@-webkit-keyframes bob-low {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes bob-low {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@media only screen and (max-width: 760px) {
  .about-container {
    grid-template-columns: 1fr;
  }
}

.classes-container {
  text-transform: lowercase;
}

.project-container {
  max-width: 700px;
}

.image-container img {
  width: 100%;
}

.image-container {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.image-set .image-container {
  display: inline-block;
}

.image-set--three .image-container {
  width: calc(33% - 3.5px);
}

.project-container .quote {
  padding-left: 1.2em;
  border-left: 4px var(--grey) solid;
  color: var(--deep);
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}

.project-container a {
  position: relative;
  color: var(--black);
  text-decoration: none;
}

.project-container a::before {
  content: "";
  position: absolute;
  background-color: var(--yella);
  width: 100%;
  height: 0.4em;
  left: 0;
  bottom: 0;
  z-index: -1;
  -webkit-transition: height 0.1s;
  transition: height 0.1s;
}

.project-container a:hover::before {
  height: 0.7em;
}

.project-container .sans {
  font-family: var(--sans-serif);
}

.fluidGraphics {
  font-family: "Questrial", sans-serif;
  color: #3a5750;
  background-color: #f5cec9;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.center {
  text-align: center;
}

.DegreesOfSuccess {
  width: 100%;
}

.SongsOfSilence-buttonContainer {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
}

.SongsOfSilence-button {
  border: none;
  background: none;
  color: var(--deep);
  font-size: 0.85em;
  font-family: var(--code);
}

.SongsOfSilence-button:hover {
  color: var(--black);
}

.SongsOfSilence-button--hide {
  visibility: hidden;
}

.SongsOfSilence-passwordInput {
  border: none;
  border-bottom: 1px solid var(--deep);
  font-size: 1em;
  font-family: var(--serif);
}

.SongsOfSilence-reddit p {
  font-family: var(--code);
  margin: 0.8em 0;
}
.SongsOfSilence-reddit em {
  color: var(--deep);
}
.SongsOfSilence-reddit blockquote {
  margin: auto 0 0 2em;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

